@import "normalize.css";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("RobotoBold"), url("fonts/roboto-v30-cyrillic_latin-700.woff2") format("woff2"), url("fonts/roboto-v30-cyrillic_latin-700.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("RobotoRegular"), url("fonts/roboto-v30-cyrillic_latin-regular.woff2") format("woff2"), url("fonts/roboto-v30-cyrillic_latin-regular.woff") format("woff");
}
@font-face {
  font-family: "TT Firs Neue";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("TTFirsNeueBold"), url("fonts/TTFirsNeue-Bold.woff2") format("woff2"), url("fonts/TTFirsNeue-Bold.woff") format("woff");
}
@font-face {
  font-family: "TT Firs Neue";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("TTFirsNeueMedium"), url("fonts/TTFirsNeue-Medium.woff2") format("woff2"), url("fonts/TTFirsNeue-Medium.woff") format("woff");
}
@font-face {
  font-family: "TT Firs Neue";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("TTFirsNeueRegular"), url("fonts/TTFirsNeue-Regular.woff2") format("woff2"), url("fonts/TTFirsNeue-Regular.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
svg {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  background-color: #FFF;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1350px;
  padding: 0 30px;
  margin: 0 auto;
}

.btn {
  padding: 0;
  cursor: pointer;
  background-color: #FF6E30;
  border: none;
}

.grid {
  display: grid;
  gap: 30px;
}

.is-label-invalid {
  position: absolute;
  font-family: "Roboto", sans-serif;
  top: 5px;
  left: 24px;
  font-size: 9px;
  font-weight: 400;
  line-height: 10.5px;
}

.section__title {
  margin: 0;
  font: 24px "TT Firs Neue", sans-serif;
  font-weight: 700;
  line-height: 31px;
}

.stop-scroll {
  overflow: hidden;
}

@media (any-hover: hover) {
  .header__search-input:hover {
    border-color: #FF6E30;
    transition: border-color 0.2s ease-in-out;
  }
  .header__nav-link:hover {
    color: #FF6E30;
    transition: color 0.2s ease-in-out;
  }
  .hero__btn:hover {
    background-color: #BC572B;
    border-color: #BC572B;
    transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  }
  .about__input:hover {
    color: #FF6E30;
    transition: color 0.2s ease-in-out;
  }
  .project__btn:hover {
    background-color: #FF6E30;
    transition: background-color 0.2s ease-in-out;
  }
  .contacts__form-input:hover {
    color: #FF6E30;
    transition: color 0.2s ease-in-out;
  }
  .socials__link:hover .socials__svg {
    color: #FF6E30;
    transition: color 0.2s ease-in-out;
  }
}
.header {
  padding: 24px 0 25px 0;
  background-color: #202020;
  margin-bottom: 41px;
}
.header__wrapper {
  position: relative;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
}
.header__form {
  width: 539px;
  position: absolute;
  right: 0;
  display: flex;
  align-items: center;
  padding: 10px;
  z-index: 14;
  transform: translateY(-75%);
  background-color: #202020;
  opacity: 0;
  visibility: hidden;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}
.header__burger-close {
  display: none;
}
.header__telephone {
  display: none;
}
.header__form--active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
.header__search-input {
  margin-right: 9px;
  flex-grow: 1;
  border: none;
  border-bottom: solid #E9E9E9 1px;
  font-family: "TT Firs Neue", sans-serif;
  color: #E9E9E9;
  font-weight: 400;
  line-height: 20.6px;
  background-color: #202020;
  outline: none;
}
.header__search-input::placeholder {
  color: #E9E9E9;
}
.header__search-input:focus-visible {
  border-color: #FF6E30;
  transition: border-color 0.2s ease-in-out;
}
.header__search-input:active {
  border-color: #CE4307;
  transition: border-color 0.2s ease-in-out;
}
.header__search-close {
  width: 23px;
  height: 23px;
  background-color: transparent;
}
.header__logo {
  grid-column: span 3;
}
.header__nav {
  grid-column: span 7;
}
.header__burger-wrapper {
  display: none;
}
.header__search {
  grid-column: span 2;
  text-align: end;
}
.header__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__search-svg {
  width: 25px;
  height: 25px;
}
.header__search {
  background-color: transparent;
}
.header__svg {
  width: 191px;
  height: 31px;
}
.header__nav-link {
  outline: none;
}
.header__nav-link:focus-visible {
  background-color: #BC572B;
  transition: background-color 0.2s ease-in-out;
}
.header__nav-link:active {
  background-color: transparent;
  color: #CE4307;
  transition: color 0.2s ease-in-out;
}
.header__item {
  font-family: "TT Firs Neue", sans-serif;
  color: #FFF;
  font-weight: 400;
  line-height: 20.6px;
}

@media (max-width: 1360px) {
  .header__svg {
    width: 166px;
    height: 27px;
  }
  .header__form {
    width: 42%;
  }
}
@media (max-width: 962px) {
  .header {
    margin-bottom: 34px;
    padding: 20px 0 0 0;
  }
  .header__wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 16px;
  }
  .header__logo {
    grid-column: span 1;
  }
  .header__nav {
    padding: 12px 27px;
    margin-left: -27px;
    margin-right: -27px;
    background-color: #4A4A4A;
    order: 3;
    grid-column: span 2;
  }
  .header__search {
    order: 2;
    grid-column: span 1;
  }
  .header__form {
    top: 0;
    width: 342px;
  }
}
@media (max-width: 576px) {
  .header {
    padding: 12px 0 0 0;
    margin-bottom: 29px;
  }
  .header__wrapper {
    gap: 12px;
  }
  .header__list {
    display: grid;
    gap: 30px;
    grid-row: span 8;
    grid-template-columns: 1fr;
  }
  .header__burger-close {
    grid-row: span 1;
  }
  .header__telephone {
    grid-row: span 9;
  }
  .header__nav {
    margin: 0;
    display: grid;
    gap: 30px;
    gap: 16px;
    grid-template-rows: repeat(18, 1fr);
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    padding: 14px 18px 36px 18px;
    background-color: #202020;
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-120%);
    transition: visibility 0.2s ease-in-out, transform 0.2s ease-in-out;
  }
  .header__svg {
    width: 112px;
    height: 18px;
  }
  .header__form {
    width: 100%;
  }
  .header__search-close {
    width: 18px;
    height: 18px;
  }
  .header__search-input {
    font-size: 14px;
    line-height: 18px;
  }
  .header__search-svg {
    width: 21px;
    height: 19px;
  }
  .header__burger-wrapper {
    padding: 12px 27px 11px 27px;
    margin-left: -27px;
    margin-right: -27px;
    background-color: #4A4A4A;
    grid-column: span 2;
    order: 3;
  }
  .header__burger-wrapper {
    display: block;
  }
  .header__burger {
    position: relative;
    width: 20px;
    height: 22px;
    background-color: transparent;
  }
  .header__nav--active {
    visibility: visible;
    transform: none;
  }
  .header__burger-close {
    display: block;
    background-color: transparent;
    width: 18px;
    height: 18px;
  }
  .header__burger-svg {
    width: 18px;
    height: 18px;
  }
  .header__telephone {
    display: block;
    align-self: flex-end;
  }
  .header__phone-svg {
    display: inline-block;
    width: 15px;
    height: 15px;
    vertical-align: middle;
  }
  .header__phone {
    display: inline-block;
    font-style: normal;
    font-family: "TT Firs Neue", sans-serif;
    font-weight: 400;
    color: #FF6E30;
    font-size: 14px;
    line-height: 18px;
    vertical-align: middle;
  }
  .burger__line {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 2px;
    background-color: #FFF;
  }
  .burger__line:first-child {
    top: 0;
  }
  .burger__line:nth-child(2) {
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
  .burger__line:last-child {
    left: 25%;
    width: 50%;
    bottom: 0;
  }
}
