}
.contacts__form-input:focus-visible {
  border-color: #FF6E30;
  transition: border-color 0.2s ease-in-out;
}
.contacts__form-input:active {
  border-color: #E9E9E9;
  transition: border-color 0.2s ease-in-out;
}
.contacts__form-input.is-invalid {
  border: #D52B1E 1px solid;
